import * as GeoDataGrpc from "../../assets/proto/dataImport/geo_data_engine_grpc_web_pb"

let client = new GeoDataGrpc.GeoDataEnginePromiseClient(
    process.env.VUE_APP_HZ_NEWS_URL
);
export default {
    //(mdbId,tableName)
    async getTableDataInDB(id,name){
        let getTableDataInDBRequest = new GeoDataGrpc.GetTableDataInDBRequest();
        getTableDataInDBRequest.setDbId(id);
        getTableDataInDBRequest.setTableName(name);
        let res = await client.getTableDataInDB(getTableDataInDBRequest,{});
        console.log("",res.toObject());
        return res.toObject()
    }
}
