<template>
    <div style="color:black;display:flex;align-items:center;justify-content:center">
      <div class="dataMateContain">
         <el-form ref="form" :model="form" label-width="80px" >
            <div class="form-row">
               <el-form-item label=":">
                  <el-select v-model="form.targetTable" placeholder="" @change="getTargetFields">
                     <el-option v-for="item in targetTables" :key="item.tableNamec" :label="item.tableNamec" 
                     :value="item.tableName" ></el-option>
                  </el-select>
               </el-form-item>
               <el-form-item label=":" style="margin-left:20px">
                  <el-select v-if="fileType === 'Access MDB'" v-model="form.originTable" placeholder="" @change="getOriginFields">
                     <el-option v-for="item in myTables" :key="item.tableName" :label="item.tableName" :value="item.tableName"></el-option>
                  </el-select>
                  <!--  this.fileType === 'Excel'  -->
                  <el-select v-else-if="fileType === 'Excel'" v-model="form.originTable" placeholder="" @change="getOriginFields">
                     <el-option v-for="item in myTables" :key="item.name" :label="item.name" :value="item.name"></el-option>
                  </el-select>
                  <!--  -->
                  <el-select v-else v-model="form.originTable" placeholder="">
                     <!--  -->
                     <el-option  label="" value=""></el-option>
                  </el-select>
                  
               </el-form-item>
            </div>
            <div>
               <el-form-item label="" style="margin-left:10px">
               </el-form-item>
               <div class="field-container">
                  <div style="width:100%;display: flex;flex-wrap: wrap;">
                     <el-form-item v-for="item in targetOptions" :key="item.fieldName" :label="`${item.fieldNamec}/${item.fieldName}`" label-width="300px" style="width:50%">
                        <el-select v-if="fileType === 'Access MDB'" v-model="selectedOptions[item.fieldId.value]" placeholder="" style="margin-right:20px;" @change="getOriginFieldValues(item, selectedOptions[item.fieldId.value])">
                           <el-option v-for="option in originOptions" :key="option.fieldName" :label="option.fieldName" :value="option.fieldName"></el-option>
                        </el-select>
                        <el-select v-else-if="fileType === 'Excel'" v-model="selectedOptions[item.fieldId.value]" placeholder="" style="margin-right:20px;" @change="getOriginFieldValues(item, selectedOptions[item.fieldId.value])">
                           <el-option v-for="option in originOptions" :key="option.fieldName" :label="option.fieldName" :value="option.fieldName"></el-option>
                        </el-select>
                        <el-select v-else v-model="selectedOptions[item.fieldId.value]" placeholder="" style="margin-right:20px;">
                           <el-option label="" value=""></el-option>
                        </el-select>
                     </el-form-item>
                  </div>
                  <div style="display:flex;margin-top: 50px;justify-content: space-between;"> 
                     <el-button @click="lastTable(tableIndex)"></el-button>
                     <el-button @click="nextTable(tableIndex)"></el-button>
                  </div>
               </div>
            </div>
         </el-form>
         <div style="margin-left: 24px;">
            <div style="display:flex;margin-top: 50px;">
               <el-button @click="back"></el-button>
               <el-button type="primary" @click="printSelectedOptions"></el-button>
            </div>
         </div>
      </div>
    </div>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import GeoDataEngine from '@/api/grpcImportApi/GeoDataEngine';
import { transposeMatrix,getValueByType,mdbMapping } from '@/utils/common';
export default {
   data() {
      return {
         tableIndex:0, //
         form: {
          targetTable:'',
          originTable:'',
        },
        originOptions:[],  //
        targetOptions:[],  //

        originTableValues:[], //mdb

        selectedOptions:{}, // id:
        tableData:[], //
      }
   },
   computed:{
      ...mapState('dataparse',['targetTables','myTables','fileType','mdbId']),
   },
   methods:{
      back(){
         this.$router.back(-1);
      },
      lastTable(index) {
         if (index - 1 < 0) {
            // 
            this.$message({
               type:'warning',
               message:''
            })
            return;
         }
         this.form.targetTable = this.targetTables[index - 1].tableNamec;
         this.getTargetFields(this.targetTables[index - 1].tableName);
      },
      nextTable(index){
         if (index + 1 > this.targetTables.length - 1) {
            // 
            this.$message({
               type:'warning',
               message:''
            })
            return;
         }
         this.form.targetTable = this.targetTables[index+1].tableNamec;
         this.getTargetFields(this.targetTables[index + 1].tableName);
      },
      getTargetFields(value){
            console.log(value);
            //
            let index = this.targetTables.findIndex(item => item.tableName === value);
            this.tableIndex = index;
            //
            const selectedItem = this.targetTables.find(item => item.tableName === value);
            if (selectedItem) {
               const targetFields = selectedItem.fieldList;
               this.targetOptions = targetFields;
            }
         
      },
      // originTableValues[]
      async getOriginFields(value){
         console.log(value)
         if(this.fileType === 'Access MDB'){
            let tableValues =await GeoDataEngine.getTableDataInDB(this.mdbId,value);
            console.log("mdb",tableValues.table);
            this.originTableValues = tableValues.table;
            // console.log(tableValues[0][0].tableText)
            const selectedItem = this.myTables.find(item => item.tableName === value);
            if(selectedItem){
               const originFields = selectedItem.fieldList;
               this.originOptions = originFields;
            }
         }
         if(this.fileType === 'Excel'){
            const selectedItem = this.myTables.find(item => item.name === value);
            if(selectedItem){
               // 
               let keysArray = [];
               // 
               for (let key in selectedItem.data[0]) {
                  keysArray.push({
                     fieldName: key,
                     // value: selectedItem.data[0][key]
                  });
               }
               this.originOptions = keysArray;
            }
         }
      },
      // 
      getOriginFieldValues(field,value){
         if(this.fileType === 'Access MDB'){
            console.log("field",field,"",value);
            //
            let index = this.originTableValues.fieldRowList.findIndex(item => item.fieldName === value);
            console.log("",index);
            //valueRowsList 
            let column = this.originTableValues.valueRowsList.map(row => row.valueRowList[index]);
            console.log("values",column);
            // tableNamefield(push)
            console.log("tableData",this.tableData);
            let existingIndex = -1;
            for (let i = 0; i < this.tableData.length; i++) {
               const item = this.tableData[i];
               if (item.tableNamec === this.form.targetTable && item.field.fieldName === field.fieldName) {
                  existingIndex = i;
                  break;
               }
            }
            // 
            if (existingIndex !== -1) {
               this.tableData.splice(existingIndex, 1, {
                  tableNamec: this.form.targetTable,
                  tableName: this.getTableName(this.form.targetTable),
                  field: {fieldName:field.fieldName,fieldNamec:field.fieldNamec,fieldType:field.fieldType,dataType:mdbMapping(field.fieldType)},
                  values: column,
               });
            } else {
               this.tableData.push({
                  tableNamec: this.form.targetTable,
                  tableName: this.getTableName(this.form.targetTable),
                  field: {fieldName:field.fieldName,fieldNamec:field.fieldNamec,fieldType:field.fieldType,dataType:mdbMapping(field.fieldType)},
                  values: column,
               });
            }
         }
         if(this.fileType === 'Excel'){
            console.log("field",field,"",value);
            const currentTable = this.myTables.find(item => item.name === this.form.originTable);
            console.log(currentTable.data);
            let column = currentTable.data.map(item => item[value]);
            console.log("values",column);
            // tableNamefield(push)
            let existingIndex = -1;
            for (let i = 0; i < this.tableData.length; i++) {
               const item = this.tableData[i];
               if (item.tableNamec === this.form.targetTable && item.field.fieldName === field.fieldName) {
                  existingIndex = i;
                  break;
               }
            }
            // 
            if (existingIndex !== -1) {
               this.tableData.splice(existingIndex, 1, {
                  tableNamec: this.form.targetTable,
                  tableName: this.getTableName(this.form.targetTable),
                  field: {fieldName:field.fieldName,fieldNamec:field.fieldNamec,fieldType:field.fieldType,dataType:mdbMapping(field.fieldType)},
                  values: column,
               });
            } else {
               this.tableData.push({
                  tableNamec: this.form.targetTable,
                  tableName: this.getTableName(this.form.targetTable),
                  field: {fieldName:field.fieldName,fieldNamec:field.fieldNamec,fieldType:field.fieldType,dataType:mdbMapping(field.fieldType)},
                  values: column,
               });
            }
         }
      },
      //
      printSelectedOptions(){
         // const selectedValues = {};
         // for (const label in this.selectedOptions){
         //    const option = this.selectedOptions[label];
         //    selectedValues[label] = option.value;
         // }
         // console.log("",this.selectedOptions);
         // console.log("tableData",this.tableData);
         console.log("()",this.tableData);
         let mergedData = {}; // 
         for (let i = 0; i < this.tableData.length; i++) {
            const item = this.tableData[i];
            const tableName = item.tableName;
            const tableNamec = item.tableNamec;
            //tableName
            if (!mergedData[tableNamec]) {
               // tableName
               mergedData[tableNamec] = {
                  tableName: tableName,
                  tableNamec: tableNamec,
                  fieldRow: [],
                  valueRows: [],
               };
            }
            // fieldvalues
            mergedData[tableNamec].fieldRow.push(item.field);
            // mergedData[tableNamec].valueRows.push(item.values);
            if(this.fileType === 'Access MDB'){
               //dataTypeobjdataType
               const newArray = item.values.map(obj => getValueByType(obj));
               mergedData[tableNamec].valueRows.push(newArray);
            }
            if(this.fileType === 'Excel'){
               mergedData[tableNamec].valueRows.push(item.values);
            }
         }
         console.log("()",mergedData)
         // 
         const mergedArray = Object.values(mergedData);
         console.log("",mergedArray);
         //valueRows
         for (let i = 0; i < mergedArray.length; i++) {
            const valueRows = mergedArray[i].valueRows;
            const transposedValueRows = transposeMatrix(valueRows);
            mergedArray[i].valueRows = transposedValueRows;
         }
         console.log("valueRows",mergedArray);
         //valueRows
         for(let i = 0; i < mergedArray.length; i++) {
            let item = mergedArray[i];
            //values,fieldsel-table
            const values = item.valueRows;
            const fields = item.fieldRow;
            const data = [];
            //values
            for (let i = 0; i < values.length; i++) {
               const row = values[i]; //
               const newObj = {};  //
               //  fields 
               for (let j = 0; j < fields.length; j++) {
                  const key = fields[j].fieldName;
                  const value = row[j];
                  newObj[key] = value;
               }
               data.push(newObj); //data
            }
            item.valueRows = data;
         }
         console.log("",mergedArray);
         this.$store.commit('dataparse/setTableData',mergedArray);
         this.$router.push('/dataPreHandle');
      },
      getTableName(tableNamec){
         console.log("tableNamec",this.targetTables.find(item => item.tableNamec === tableNamec).tableName);
         return this.targetTables.find(item => item.tableNamec === tableNamec).tableName;
      }
   },
   mounted(){
      //
      this.form.targetTable = this.targetTables[0].tableNamec;
      this.getTargetFields(this.targetTables[0].tableName);
      console.log("targetTables",this.targetTables);
      console.log("myTables",this.myTables);
      console.log("fileType",this.fileType)
   },
}
</script>
<style scoped>
.dataMateContain {
   width: 92%;
   height: 100%;
   padding: 20px;
}
.form-row {
  display: flex;
}
.field-container {
   margin-left:20px;
   box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
   border:1px solid #E6E6E6;
   padding: 30px 80px;
   width:100%;
   border-radius: 10px;
}
:deep(.el-form-item__label){
   text-align: left;
}

</style>
